main {
  flex: 1;

  article {
    display: flex;
    flex-direction: column;
    width: min(100% - 2em);
    margin-block: 2em 1em;
    margin-inline: auto;
  }

  h1 {
    font-weight: bold;
    margin-bottom: 1em;
    @include visually-hidden;
  }

  h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 1em;
  }

  p {
    margin-bottom: 1em;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

// label {
//   display: inline-block;
//   font-family: inherit;
//   font-weight: bold;
// }

textarea {
  --scroll-dimension: 0.25rem;
  --scroll-border-radius: 1em;
  --scroll-color-thumb: rgba(0, 0, 0, 0.5);
  --scroll-color-track: rgba(0, 0, 0, 0);
  --scroll-background: transparent;

  min-height: 200px;
  max-height: 450px;
  border: 1px solid var(--theme-accent);
  border-radius: 0.25em;
  resize: vertical;
  padding: 1em;
  -ms-overflow-style: inherit;
  scrollbar-width: thin;
  scrollbar-color: var(--scroll-color-thumb) var(--scroll-color-track);

  &::-webkit-scrollbar {
    display: block;
    width: var(--scroll-dimension);
    height: var(--scroll-dimension);
  }

  ::-webkit-scrollbar-button:single-button {
    width: 0;
    height: 0;
  }

  ::-webkit-scrollbar-button:single-button:hover {
    width: 0;
    height: 0;
  }

  &::-webkit-scrollbar-track {
    background-color: var(--scroll-color-track);
    border: var(--scroll-dimension) solid var(--scroll-background);
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--scroll-color-thumb);
  }

  &:focus,
  &:focus-visible {
    outline: 2px solid var(--theme-accent);
    outline-offset: 5px;
  }
}

.content {
  position: relative;

  &__item {
    &[data-open="false"] {
      display: none;
    }

    &[data-open="true"] {
      display: block;
    }

    ol {
      padding-inline-start: 20px;
    }

    code {
      font-family: monospace;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;

  &__group {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-bottom: 1em;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__input {
    position: absolute;
    opacity: 0;
    width: 0;

    &:focus ~ label,
    &:focus-visible ~ label {
      outline: 2px solid var(--theme-accent);
      outline-offset: 5px;
    }
  }

  &__label {
    display: inline-block;
    text-align: center;
    font-weight: initial;
    border-radius: 0.25em;
    padding: 0.5em 1em;
    color: var(--theme-text-alt);
    border: 1px solid var(--theme-accent);
    background-color: var(--theme-accent);
    transition: var(--theme-transition);
    cursor: pointer;

    &:hover {
      color: var(--theme-accent);
      background-color: $color-transparent;
    }
  }

  &__button {
    border-radius: 0.25em;
    padding: 0.5em 1em;
    margin-bottom: 2em;
    color: var(--theme-text-alt);
    border: 1px solid var(--theme-accent);
    background-color: var(--theme-accent);
    transition: var(--theme-transition);

    &[aria-hidden="true"] {
      display: none;
    }

    &[aria-hidden="false"] {
      display: block;
    }

    &:hover {
      color: var(--theme-accent);
      background-color: $color-transparent;
    }

    &:focus,
    &:focus-visible {
      outline: 2px solid var(--theme-accent);
      outline-offset: 5px;
    }
  }

  &__download {
    color: #000;
    border-radius: 0.25em;
    padding: 0.5em 1em;
    text-decoration: none;
    text-align: center;
    color: var(--theme-text-alt);
    border: 1px solid var(--theme-accent);
    background-color: var(--theme-accent);
    transition: var(--theme-transition);

    &:hover {
      color: var(--theme-accent);
      background-color: $color-transparent;
    }

    &:focus,
    &:focus-visible {
      outline: 2px solid var(--theme-accent);
      outline-offset: 5px;
    }
  }
}

.dropdown {
  position: relative;

  &__group {
    display: flex;
    align-items: center;
    border: 1px solid var(--theme-accent);
    border-radius: 0.25em;
    margin-bottom: 1em;
  }

  &__input {
    border: 0;
    padding-inline: 1em;
    width: 100%;
    margin-inline: 1em;

    &:focus {
      outline-color: transparent;
    }
  }

  &__button {
    border: 0;
    border-radius: 0;
    margin: auto 0 0 0;
    padding: 0.5em;
    color: var(--theme-text-alt);
    border-left: 1px solid var(--theme-accent);
    background-color: var(--theme-accent);
    transition: var(--theme-transition);

    &[aria-expanded="true"] {
      .dropdown__icon {
        transform: rotate(180deg);
      }
    }

    &:hover {
      color: var(--theme-accent);
      background-color: $color-transparent;
    }

    &:focus,
    &:focus-visible {
      outline: 2px solid var(--theme-accent);
      outline-offset: 5px;
    }
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
  }

  &__option {
    display: none;

    &.is__active {
      display: block;
    }
  }

  &__items {
    width: 100%;
    border: 1px solid rgba(0 0 0 / 10%);
    border-radius: 0.25em;
    padding: 0.5em 1em;
    margin-bottom: 0.25em;
    border: 1px solid var(--theme-accent);
    color: var(--theme-accent);
    background-color: $color-transparent;
    transition: var(--theme-transition);

    &:hover {
      color: var(--theme-text-alt);
      background-color: var(--theme-accent);
    }

    &:focus,
    &:focus-visible {
      outline: 2px solid var(--theme-accent);
      outline-offset: 5px;
    }
  }
}

.wheel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &__option {
    width: 100%;
    border: 1px solid var(--theme-text);
    border-radius: 0.25em;

    > input[type="checkbox"] {
      position: absolute;
      left: -9999px;
      width: 0;
      height: 0;
    }

    > input {
      &:focus {
        ~ {
          .wheel__label {
            outline: 2px solid var(--theme-accent);
            outline-offset: 5px;
            border-radius: 0.25em;
          }
        }
      }

      &:checked {
        ~ {
          .wheel__label {
            border-bottom: 1px solid var(--theme-text);

            &::after {
              top: 1em;
              transform: rotate(225deg);
            }
          }

          .wheel__entry {
            opacity: 1;
            padding: 0.5em 0.5em;
            max-height: 99rem;
            transition: max-height 0.5s ease-in-out;
          }
        }
      }
    }
  }

  &__label {
    display: block;
    position: relative;
    padding: 0.5em;
    border-bottom: 1px solid transparent;
    color: var(--theme-text-alt);
    background-color: var(--theme-text);
    transition: var(--theme-transition);

    &:hover {
      opacity: 0.75;
    }

    &::after {
      content: "";
      position: absolute;
      width: 10px;
      height: 10px;
      border-bottom: 2px solid;
      border-right: 2px solid;
      transform: rotate(45deg);
      top: 0.75em;
      right: 1em;
      @include transition;
    }
  }

  &__entry {
    display: flex;
    flex-direction: column;
    opacity: 0;
    max-height: 0;
    margin: 0 0.5em;
    overflow: hidden;
    transition: max-height 0.5s cubic-bezier(0, 1, 0, 1),
      opacity 01s cubic-bezier(0, 1, 0, 1);

    label {
      margin-bottom: 1em;
    }

    textarea {
      resize: vertical;
      min-height: 150px;
      overflow: auto;
      padding: 0.5em;
      margin-bottom: 1em;
      border: 1px solid var(--theme-text);
      border-radius: 0.25em;

      &:focus {
        outline: 2px solid var(--theme-accent);
        outline-offset: 5px;
      }
    }
  }

  // &::-webkit-scrollbar {
  //   width: 5px;
  // }
  // &::-webkit-scrollbar-track {
  //   background: transparent;
  // }
  // &::-webkit-scrollbar-thumb {
  //   background-color: $background-color_1;
  //   border-radius: 0.25em;
  //   border: transparent;
  // }

  &__audio {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: .25rem;

    > label {
      position: relative;
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin: 0;
      user-select: none;
    }

    input {
      &:focus + .wheel__checkmark {
        outline: 2px solid var(--theme-accent);
        outline-offset: 5px;
      }

      &:checked ~ .wheel__checkmark {
        background-color: var(--theme-text);
        opacity: 1;
      }

      &:checked ~ .wheel__checkmark {
        &:after {
          display: flex;
          left: 8px;
          top: 3px;
          width: 7px;
          height: 14px;
          border: solid #fff;
          border-width: 0 3px 3px 0;
          border: solid var(--theme-text-alt);
          border-width: 0 3px 3px 0;
          transform: rotate(45deg);
        }
      }
    }
  }

  &__checkmark {
    position: absolute;
    top: 0;
    right: 0;
    height: 25px;
    width: 25px;
    border: 1px solid var(--theme-text);
    // opacity: var(--theme-disabled);
    cursor: pointer;

    &:after {
      content: "";
      position: absolute;
      display: none;
    }
  }

  &__content {
    --wheel-border-before: 10px;
    --wheel-border-top-before: 20px;
    --wheel-top-after: 3px;
    --wheel-border-after: 5px;
    --wheel-border-top-after: 10px;

    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    margin: 0.5em;
    max-width: 100%;
    max-height: auto;
    cursor: none;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      border-left: var(--wheel-border-before) solid transparent;
      border-right: var(--wheel-border-before) solid transparent;
      border-top: var(--wheel-border-top-before) solid var(--theme-text);
      z-index: 1;
    }

    &::after {
      content: "";
      position: absolute;
      top: var(--wheel-top-after);
      border-left: var(--wheel-border-after) solid transparent;
      border-right: var(--wheel-border-after) solid transparent;
      border-top: var(--wheel-border-top-after) solid var(--theme-text-alt);
      z-index: 10;
    }
  }

  &__canvas {
    display: block;
    max-width: 100%;
    image-rendering: auto;
  }

  &__center {
    --wheel-center-size: 13px;
    --wheel-center-border: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: var(--wheel-center-size);
    height: var(--wheel-center-size);
    background-color: var(--theme-text-alt);
    border: var(--wheel-center-border) solid var(--theme-text);
    border-radius: 50%;
    transition: 0.8s;
  }

  &__result {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 56px;
    width: 100%;
    padding: 0.5em;
    margin-bottom: 1em;
    border: 2px dashed var(--theme-text);
    border-radius: 0.25em;
    font-weight: bold;
    font-size: 1.5rem;
    line-height: 1;

    &:has(span:empty) {
      border-color: transparent;
    }
  }

  &__item {
    font-weight: bold;
  }

  &__spin,
  &__remove,
  &__reset {
    font: inherit;
    border: 0;
    width: 100%;
    padding: 0.5em;
    border: 1px solid var(--theme-text);
    border-radius: 0.25em;
    margin-bottom: 1em;
    color: var(--theme-text-alt);
    background-color: var(--theme-text);
    transition: var(--theme-transition);
    cursor: pointer;

    &:disabled {
      opacity: var(--theme-disabled);
      cursor: none;
    }

    &:hover {
      opacity: 0.75;

      &:disabled {
        opacity: var(--theme-disabled);
      }
    }

    &:focus,
    &:focus-visible {
      outline: 2px solid var(--theme-accent);
      outline-offset: 5px;
    }
  }

  &__spin {
    font-weight: bold;
    color: var(--theme-text);
    background-color: var(--theme-accent);
    border: 1px solid var(--theme-accent);
    transition: box-shadow 0.25s ease-in-out;

    &:hover {
      opacity: 1;
      box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
    }

    &:focus,
    &:focus-visible {
      outline: 2px solid var(--theme-text);
      outline-offset: 5px;
    }
  }
}

// Breakpoint
@include media-breakpoint-up(sm) {
  .wheel {
    &__content {
      --wheel-border-before: 15px;
      --wheel-border-top-before: 30px;
      --wheel-top-after: 5px;
      --wheel-border-after: 7px;
      --wheel-border-top-after: 15px;
    }
    &__center {
      --wheel-center-size: 20px;
      --wheel-center-border: 5px;
    }
  }
}

@include media-breakpoint-up(lg) {
  main {
    section {
      article {
        @include media-container(xl);
      }
    }
  }
}
