// Splash
.splash {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  z-index: 100;
  background-color: var(--theme-bg);

  &__item {
    font-size: 5rem;
    font-weight: 800;
    animation: pulse 2.5s infinite, zoom-out 1s linear 2.5s;
  }
}

@media (prefers-reduced-motion) {
  .splash {
    animation: none;
  }
}

// Loader
.loader {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  inset: 0;
  z-index: 1;

  &__item {
    width: 20px;
    height: 20px;
    border-radius: 100%;
    display: inline-block;
    animation: slide 1s infinite;

    @for $i from 1 through 5 {
      &:nth-child(#{$i}) {
        animation-delay: (0.1s * $i);
        background-color: darken(#0b4db7, (5 * $i));
      }
    }
  }
}

@keyframes slide {
  0% {
    transform: scale(1);
  }
  50% {
    opacity: 0.3;
    transform: scale(2);
  }
  100% {
    transform: scale(1);
  }
}

@media (prefers-reduced-motion) {
  .loader {
    animation: none;
  }
}
